import React, { Suspense } from 'react'
import { Route, Switch, HashRouter } from 'react-router-dom'
import styled from 'styled-components'
import { ResetCSS } from 'uikit'
import BigNumber from 'bignumber.js'
import { useFetchPublicData } from 'state/hooks'
import GlobalStyle from './style/Global'
import Menu from './components/Menu'
import SuspenseWithChunkError from './components/SuspenseWithChunkError'
import PageLoader from './components/PageLoader'
import EasterEgg from './components/EasterEgg'
import { RedirectOldRemoveLiquidityPathStructure } from './views/RemoveLiquidity/redirects'
import { RedirectDuplicateTokenIds, RedirectOldAddLiquidityPathStructure } from './views/AddLiquidity/redirects'
import { RedirectPathToSwapOnly } from './views/Swap/redirects'
// Route-based code splitting
// Only pool is included in the main bundle because of it's the most visited page
import Home from './views/Home'
import Swap from'./views/Swap'
import Pool from './views/Pool'
import PoolFinder from './views/PoolFinder'
import AddLiquidity from './views/AddLiquidity'
import RemoveLiquidity from './views/RemoveLiquidity'
import NotFound from './views/NotFound'
import LpPools from './views/LpPools'
import StakePools from './views/StakePools/Syruppoolswrapper'
import Web3ReactManager from './components/Web3ReactManager'
import Popups from './components/Popups'

const AppWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  overflow-x: hidden;
`

const BodyWrapper = styled.div`
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1;
  margin-bottom: 64px;
  ${({ theme }) => theme.mediaQueries.lg} {
    margin-bottom: 0;
  }
`

// This config is required for number formatting
BigNumber.config({
  EXPONENTIAL_AT: 1000,
  DECIMAL_PLACES: 80,
})

const App: React.FC = () => {
  useFetchPublicData()

  return (
    <Suspense fallback={null}>
     <HashRouter>
        <AppWrapper>
      <ResetCSS />
      <GlobalStyle />
      <Menu>
      <BodyWrapper>
        <Popups />
         <Web3ReactManager>
        <SuspenseWithChunkError fallback={<PageLoader />}>
          <Switch>
            <Route path="/" exact>
              <Home />
            </Route>
            <Route exact strict path="/swap" component={Swap} />
            <Route exact strict path="/find" component={PoolFinder} />
            <Route exact strict path="/pool" component={Pool} />
            <Route exact path="/lppools">
              <LpPools />
            </Route>
            <Route exact path="/stpools">
              <StakePools />
            </Route>
            <Route exact path="/add" component={AddLiquidity} />
            <Route exact strict path="/remove/:currencyIdA/:currencyIdB" component={RemoveLiquidity} />
            <Route exact path="/add/:currencyIdA" component={RedirectOldAddLiquidityPathStructure} />
            <Route exact path="/add/:currencyIdA/:currencyIdB" component={RedirectDuplicateTokenIds} />
            <Route exact strict path="/remove/:tokens" component={RedirectOldRemoveLiquidityPathStructure} />
            <Route component={RedirectPathToSwapOnly} />
            
            {/* 404 */}
            <Route component={NotFound} />
          </Switch>
        </SuspenseWithChunkError>
        </Web3ReactManager>
        </BodyWrapper>
      </Menu>
      <EasterEgg iterations={2} />
      </AppWrapper>
      </HashRouter>
    </Suspense>
  )
}

export default React.memo(App)
