import { ConnectorNames } from 'uikit'
import { Web3Provider } from '@ethersproject/providers'
import { InjectedConnector } from '@web3-react/injected-connector'
import { WalletConnectConnector } from '@web3-react/walletconnect-connector'
import { BscConnector } from '@binance-chain/bsc-connector'
import { ENVChainId } from 'sdk'
import getNodeUrl from 'utils/getRpcUrl'
import { NetworkConnector } from './NetworkConnector'

export const network = new NetworkConnector({
  urls: { [ENVChainId]: getNodeUrl(ENVChainId) },

  defaultChainId: ENVChainId
})

let networkLibrary: Web3Provider | undefined

export function getNetworkLibrary(): Web3Provider {
  // eslint-disable-next-line no-return-assign
  return (networkLibrary = networkLibrary ?? new Web3Provider(network.provider as any))
}

export const injected = new InjectedConnector({
  supportedChainIds: [ENVChainId],
})

export const bscConnector = new BscConnector({ supportedChainIds: [ENVChainId] })

// mainnet only
export const walletconnect = new WalletConnectConnector({
  rpc: { [ENVChainId]: getNodeUrl(ENVChainId) },

  bridge: 'https://bridge.walletconnect.org',

  qrcode: true,

  pollingInterval: 15000,
})

// mainnet only
export const connectorsByName: { [connectorName in ConnectorNames]: any } = {
  [ConnectorNames.Injected]: injected,

  [ConnectorNames.WalletConnect]: walletconnect,

  [ConnectorNames.BSC]: bscConnector,
}
