import { InjectedConnector } from '@web3-react/injected-connector'
import { WalletConnectConnector } from '@web3-react/walletconnect-connector'
import { BscConnector } from '@binance-chain/bsc-connector'
import { ConnectorNames } from 'uikit'
import Web3 from 'web3'
import { ENVChainId } from 'sdk'
import getNodeUrl from './getRpcUrl'

const POLLING_INTERVAL = 12000

const injected = new InjectedConnector({ supportedChainIds: [ENVChainId] })

const walletconnect = new WalletConnectConnector({
  rpc: { [ENVChainId]: getNodeUrl(ENVChainId) },

  bridge: 'https://pancakeswap.bridge.walletconnect.org/',

  qrcode: true,

  pollingInterval: POLLING_INTERVAL,
})

const bscConnector = new BscConnector({ supportedChainIds: [ENVChainId] })

export const connectorsByName: { [connectorName in ConnectorNames]: any } = {
  [ConnectorNames.Injected]: injected,

  [ConnectorNames.WalletConnect]: walletconnect,
  
  [ConnectorNames.BSC]: bscConnector,
}

export const getLibrary = (provider): Web3 => {
  return provider
}
