import { ChainId}  from './constants'

export interface Address {
  [ChainId.BSCNET]: string

  [ChainId.HTNET]: string
}

export interface Token {
  symbol: string
  address?: Address
  decimals?: number
  projectLink?: string
}

export enum PoolCategory {
  'COMMUNITY' = 'Community',
  'CORE' = 'Core',
  'ETH' = 'ETH'
}

export type Images = {
  lg: string
  md: string
  sm: string
  ipfs?: string
}

export type PageMeta = {
  title: string
  description?: string
  image?: string
}

export interface SyrupPoolConfig {
  syrupId: number
  earningToken: Token
  stakingToken: Token
  poolCategory: PoolCategory
  stakingLimit?: number
  contractAddress: Address
  tokenPerBlock: string
  sortOrder?: number
  harvest?: boolean
  isFinished?: boolean
  enableEmergencyWithdraw?: boolean
}
