import React from 'react'
import { RefreshContextProvider } from 'contexts/RefreshContext'
import Syruppoolsagent from './Syruppoolsagent'

const Syruppoolwrapper: React.FC = () => {
    return (
      <RefreshContextProvider>
        <Syruppoolsagent />
      </RefreshContextProvider>
    );
}

export default Syruppoolwrapper