import { ENVChainId } from 'sdk'
import { useCallback } from 'react'
import { useAppDispatch } from 'state'
import { useWeb3React } from '@web3-react/core'
import { updateUserPendingReward, updateUserBalance } from 'state/actions'
import { useSyrupPoolContract } from './useContract'
import { calculateGasMargin } from '../utils'

export default function useSyrupHarvest(
  syrupId, callback: any
): [ () => Promise<void>] {
  const { account } = useWeb3React()

  const dispatch = useAppDispatch()

  const chainId = ENVChainId

  const syrupPoolContract = useSyrupPoolContract(syrupId, chainId)
  
  const onReward = useCallback(async (): Promise<void> => {
    if (!syrupPoolContract) {
      console.error('syrup pool contract is null')

      return
    }

    const estimatedGas = await syrupPoolContract.estimateGas.getReward()
                                                      
    // eslint-disable-next-line consistent-return
    syrupPoolContract
           .getReward({ gasLimit: calculateGasMargin(estimatedGas) })
           .then(() => {
              dispatch(updateUserPendingReward(syrupId, account))

              dispatch(updateUserBalance(syrupId, account))

              callback()
      })
      .catch((error: Error) => {
        console.error('Failed to harvest token', error)
        throw error
      })
    
  }, [syrupPoolContract, dispatch, syrupId, account, callback])

  return [ onReward ]
}

 
