import { ENVChainId, BIG_TEN } from 'sdk'
import { useCallback } from 'react'
import { useAppDispatch } from 'state'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core'
import { updateUserStakedBalance, updateUserBalance } from 'state/actions'
import { useSyrupPoolContract } from './useContract'
import { calculateGasMargin } from '../utils'

export default function useSyrupStake(
  syrupId, callback: any
): [ (amount: string, decimals: number) => Promise<void>] {
  const { account } = useWeb3React()

  const dispatch = useAppDispatch()

  const chainId = ENVChainId

  const syrupPoolContract = useSyrupPoolContract(syrupId, chainId)
  
  const onStake = useCallback(async (amount: string, decimals = 18): Promise<void> => {
    if (!syrupPoolContract) {
      console.error('syrup pool contract is null')

      return
    }

    const estimatedGas = await syrupPoolContract.estimateGas.deposit(new BigNumber(amount).times(BIG_TEN.pow(decimals)).toString())
                                                      
    // eslint-disable-next-line consistent-return
    syrupPoolContract
           .deposit((new BigNumber(amount).times(BIG_TEN.pow(decimals)).toString()), { gasLimit: calculateGasMargin(estimatedGas) })
           .then(() => {
              dispatch(updateUserStakedBalance(syrupId, account))

              dispatch(updateUserBalance(syrupId, account))

              callback()
      })
      .catch((error: Error) => {
        console.error('Failed to stake token', error)
        throw error
      })
    
  }, [syrupPoolContract, dispatch, syrupId, account, callback])

  return [ onStake ]
}