import Web3 from 'web3'
import { AbiItem } from 'web3-utils'
import getWeb3NoAccount from 'utils/web3'
 
// Addresses
import 
{
  getGrausLpAddress,
  getGrausAddress,
} from 'utils/addressHelpers'

// ABI
import { ERC20, ERC721, ChainId } from 'sdk'
import syruppools from 'sdk/syruppools'

import pairABIs from 'sdk/abis/pairABI'
import syruppoolABI from 'sdk/abis/syruppool.json'

const getContract = (abi: any, address: string, chainId : ChainId, web3?: Web3) => {
  const _web3 = web3 ?? getWeb3NoAccount(chainId)

  return new _web3.eth.Contract((abi as unknown) as AbiItem, address)
}

export const getGrausLpContract = (chainId : ChainId, web3?: Web3) =>
{
  return getContract(pairABIs[chainId], getGrausLpAddress(chainId), chainId, web3)
}

export const getGrausContract = (chainId : ChainId, web3?: Web3) =>
{
  return getContract(ERC20, getGrausAddress(chainId), chainId, web3)
}

export const getBep20Contract = (address: string, chainId : ChainId, web3?: Web3) => {
  return getContract(ERC20, address, chainId, web3)
}

export const getErc721Contract = (address: string, chainId : ChainId, web3?: Web3) => {
  return getContract(ERC721, address, chainId, web3)
}

export const getSyrupPoolContract = (id: number, chainId : ChainId, web3?: Web3) => {
  const config = syruppools.find((pool) => pool.syrupId === id)

  return getContract(syruppoolABI, config.contractAddress[chainId], chainId, web3)
}
