import React, { useMemo } from 'react'
import FarmsPageHeader from 'components/FarmsPageHeader'
import Page from 'components/layout/Page'
import { useTranslation } from 'contexts/Localization'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core'
import { Heading, Flex } from 'uikit'
import orderBy from 'lodash/orderBy'
import partition from 'lodash/partition'
import usePersistState from 'hooks/usePersistState'
import { useSyrupPools, useBlock } from 'state/hooks'
import FlexLayout from 'components/layout/Flex'
import STPoolCard from './STPoolCard'
import STPoolTabButtons from './STPoolTabButtons'

const StakePools: React.FC = () => {
    const { t } = useTranslation()

    const { account } = useWeb3React()

    const pools = useSyrupPools(account)

    const { currentBlock } = useBlock()

    const [stakedOnly, setStakedOnly] = usePersistState(false, 'grampus_syrup_pool_staked')

    const [showHistory, setShowHistory] = usePersistState(true, 'grampus_syrup_pool_history')
 
    const [finishedPools, openPools] = useMemo(
      () => partition(pools, (pool) => pool.isFinished || (currentBlock > pool.endBlock && pool.endBlock > 0)),
      [currentBlock, pools],
    )

    const stakedOnlyFinishedPools = useMemo(
      () => finishedPools.filter((pool) => pool.userData && new BigNumber(pool.userData.stakedBalance).isGreaterThan(0)),
      [finishedPools],
    )

    const stakedOnlyOpenPools = useMemo(
      () => openPools.filter((pool) => pool.userData && new BigNumber(pool.userData.stakedBalance).isGreaterThan(0)),
      [openPools],
    )

    const hasStakeInFinishedPools = stakedOnlyFinishedPools.length > 0

    return (
      <>
        <FarmsPageHeader>
         <Flex justifyContent="space-between" flexDirection={['column', null, 'row']}>
            <Flex flexDirection="column" mr={['8px', 0]}>
              <Heading as="h1" scale="xxl" color="secondary" mb="24px">
                {t('Syrup Pools')}
              </Heading>
              <Heading scale="md" color="text">
                {t('stake some tokens to earn.')}
              </Heading>
            </Flex>
          </Flex>
        </FarmsPageHeader>
        <Page>
        <STPoolTabButtons
          showHistory={showHistory}
          setShowHistory={setShowHistory}
          stakedOnly={stakedOnly}
          setStakedOnly={setStakedOnly}
          hasStakeInFinishedPools={hasStakeInFinishedPools}
        />
        { !showHistory ? <FlexLayout>
            <> 
              {stakedOnly
                ? orderBy(stakedOnlyOpenPools, ['sortOrder']).map((pool) => (
                    <STPoolCard key={pool.syrupId} pool={pool} account={account} />
                  ))
                : orderBy(openPools, ['sortOrder']).map((pool) => (
                    <STPoolCard key={pool.syrupId} pool={pool} account={account} />
                  ))}
            </>
        </FlexLayout>
        : <FlexLayout> 
          <> 
          {stakedOnly
              ? orderBy(stakedOnlyFinishedPools, ['sortOrder']).map((pool) => (
                  <STPoolCard key={pool.syrupId} pool={pool} account={account} />
                ))
              : orderBy(finishedPools, ['sortOrder']).map((pool) => (
                  <STPoolCard key={pool.syrupId} pool={pool} account={account} />
                ))} 
           </>
        </FlexLayout> }
      </Page>
      </>
    )   
}
 
export default StakePools
