import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { ENVChainId, BIG_TEN } from 'sdk'
import BigNumber from 'bignumber.js'
import { useAppDispatch } from 'state'
import {
  updateUserStakedBalance,
  updateUserBalance,
  updateUserPendingReward,
} from 'state/actions'
import { useSyrupPoolContract } from './useContract'
import { calculateGasMargin } from '../utils'

export default function useSyrupUnstake(
  syrupId, enableEmergencyWithdraw = false, callback: any
): [ (amount: string, decimals: number) => Promise<void>] {
  const { account } = useWeb3React()

  const dispatch = useAppDispatch()

  const chainId = ENVChainId

  const syrupPoolContract = useSyrupPoolContract(syrupId, chainId)
  
  const onUnstake = useCallback(async (amount: string, decimals = 18): Promise<void> => {
    if (!syrupPoolContract) {
      console.error('syrup pool contract is null')

      return
    }

    if (enableEmergencyWithdraw) {
      const estimatedGas_em = await syrupPoolContract.estimateGas.emergencyWithdraw()
                                                      
      // eslint-disable-next-line consistent-return
      syrupPoolContract
             .emergencyWithdraw({ gasLimit: calculateGasMargin(estimatedGas_em) })
             .then(() => {
                callback()
  
                dispatch(updateUserStakedBalance(syrupId, account))
  
                dispatch(updateUserBalance(syrupId, account))
          
                dispatch(updateUserPendingReward(syrupId, account))
        })
        .catch((error: Error) => {
          console.error('Failed to approve token', error)
          throw error
        })
    } 
    else {
      const estimatedGas = await syrupPoolContract.estimateGas.withdraw(new BigNumber(amount).times(BIG_TEN.pow(decimals)).toString())
                                                      
      // eslint-disable-next-line consistent-return
      syrupPoolContract
             .withdraw((new BigNumber(amount).times(BIG_TEN.pow(decimals)).toString()), { gasLimit: calculateGasMargin(estimatedGas) })
             .then(() => {  
                dispatch(updateUserStakedBalance(syrupId, account))
  
                dispatch(updateUserBalance(syrupId, account))
          
                dispatch(updateUserPendingReward(syrupId, account))

                callback()
        })
        .catch((error: Error) => {
          console.error('Failed to unstake token', error)
          throw error
        })
    }
    
  }, [syrupPoolContract, dispatch, syrupId, account, callback, enableEmergencyWithdraw])

  return [ onUnstake ]
}
