import React, { useState, useCallback } from 'react'
import { ENVChainId } from 'sdk'
import { Button, AutoRenewIcon, Skeleton } from 'uikit'
import { ApprovalState } from 'hooks/useApproveCallback'
import useSyrupApprove from 'hooks/useSyrupApprove'
import { useTranslation } from 'contexts/Localization'
import useToast from 'hooks/useToast'
import { SyrupPool } from 'state/types'

interface ApprovalActionProps {
  pool: SyrupPool

  isLoading?: boolean

  setNeedsApproval
}

const ApprovalAction: React.FC<ApprovalActionProps> = ({ pool, isLoading = false, setNeedsApproval}) => {
  const { syrupId, stakingToken, earningToken, isFinished } = pool

  const { t } = useTranslation()

  const chainId = ENVChainId

  const [requestedApproval, setRequestedApproval] = useState(false)

  const callback = () =>
  {
    toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now stake in the ${earningToken.symbol} pool!`)}`)
       
    setRequestedApproval(false)

    setNeedsApproval(false)
  }

  const [ approvalState, approveCallback ] = useSyrupApprove(stakingToken, syrupId, chainId, callback)

  const { toastSuccess, toastError } = useToast()

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)

      await approveCallback()
      
      if (approvalState ===  ApprovalState.UNKNOWN) {
        // user rejected tx or didn't go thru
        toastError(
          `${t('Error')}`,
          `${t(`Please try again. Confirm the transaction and make sure you are paying enough gas!`)}`,
        )

        setRequestedApproval(false)
      }  else if (approvalState === ApprovalState.APPROVED)
      {
        toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now stake in the ${earningToken.symbol} pool!`)}`)
       
        setRequestedApproval(false)
    
        setNeedsApproval(false)
      }
    } catch (e) {
      console.error(e)
      toastError('Error', e?.message)
    }
  }, [approvalState, approveCallback, setRequestedApproval, toastError, t, earningToken, setNeedsApproval, toastSuccess])

  return (
    <>
      {isLoading ? (
        <Skeleton width="100%" height="52px" />
      ) : (
        <Button
          isLoading={requestedApproval}
          endIcon={requestedApproval ? <AutoRenewIcon spin color="currentColor" /> : null}
          disabled={isFinished || requestedApproval}
          onClick={handleApprove}
          width="100%"
        >
          {t('Enable')}
        </Button>
      )}
    </>
  )
}

export default ApprovalAction
