import { ETHs, ETHERIds, ChainId } from 'sdk'
import Token from 'sdk/entities/token'
import Currency from 'sdk/entities/currency'

export function checkCurrencyId(currency: Currency, chainId : ChainId): string {
  if (currency === ETHs[chainId])
  {
    const mainNetChainId = ChainId.HTNET

    return ETHERIds[chainId] ? ETHERIds[chainId] : ETHERIds[mainNetChainId]
  } 

  if (currency instanceof Token) return currency.address
  
  throw new Error('invalid currency')
}

export default checkCurrencyId
