import { ChainId, ENVChainId, routerAddress, Address, addresses} from 'sdk'
import tokens from 'sdk/tokens'
import syruppools from 'sdk/syruppools'

export const getAddress = (chainId : ChainId, address: Address): string => {
  return address[chainId] ? address[chainId] : address[ENVChainId]
}

export const getRouterAddress = (chainId : ChainId) => {
  return getAddress(chainId, routerAddress)
}

export const getGrausLpAddress = (chainId : ChainId) =>
{
   return getAddress(chainId, addresses.grauslp)
}

export const getGrausAddress = (chainId : ChainId) => {
  return getAddress(chainId, tokens.graus.address)
}

export const getGrausDecimals = () =>
{
  return tokens.graus.decimals
}

export const getSyrupPoolContractAddress = (id: number, chainId : ChainId) => {
  const config = syruppools.find((pool) => pool.syrupId === id)

  return  config.contractAddress[chainId]
}
 