import { useCallback, useMemo } from 'react'
import BigNumber from 'bignumber.js'
import { MaxUint256 } from '@ethersproject/constants'
import { useWeb3React } from '@web3-react/core'
import { ChainId, Token } from 'sdk'
import { getSyrupPoolContractAddress } from 'utils/addressHelpers'
import { useAppDispatch } from 'state'
import { updateUserAllowance } from 'state/actions'
import { calculateGasMargin } from '../utils'
import { ApprovalState } from './useApproveCallback'
import { useTokenContract } from './useContract'
import { useSingleCallResult } from '../state/multicall/hooks'

export default function useSyrupApprove(
  token: Token, syrupId, chainId : ChainId, callback: any
): [ApprovalState, () => Promise<void>] {
  const { account } = useWeb3React()

  const dispatch = useAppDispatch()
  
  const syrupPoolContract = getSyrupPoolContractAddress(syrupId, chainId)

  const tokenContract = useTokenContract(token.address[chainId])

  const inputs = useMemo(() => [account, syrupPoolContract], [account, syrupPoolContract])

  const allowance = useSingleCallResult(tokenContract, 'allowance', inputs).result

  const [ currentallowance ] = useMemo(() => [allowance ? new BigNumber(allowance.toString()) : undefined], [allowance])

  // check the current approval status
  const approvalState: ApprovalState = useMemo(() => {
    if (!token || !syrupPoolContract) return ApprovalState.UNKNOWN

    if (!currentallowance) return ApprovalState.UNKNOWN

    // amountToApprove will be defined if currentAllowance is
    return currentallowance.gt(0) ? ApprovalState.APPROVED : ApprovalState.NOT_APPROVED
  }, [token, syrupPoolContract, currentallowance])

  const approve = useCallback(async (): Promise<void> => {
    if (approvalState !== ApprovalState.NOT_APPROVED) {
      console.error('approve was called unnecessarily')

      return
    }

    if (!tokenContract) {
      console.error('tokenContract is null')

      return
    }

    if (!syrupPoolContract) {
      console.error('no spender')

      return
    }

    const estimatedGas = await tokenContract.estimateGas.approve(syrupPoolContract, MaxUint256)

    // eslint-disable-next-line consistent-return
    tokenContract
           .approve(syrupPoolContract, MaxUint256, { gasLimit: calculateGasMargin(estimatedGas) })
           .then(() => {
            dispatch(updateUserAllowance(syrupId, account))

            callback()
      })
      .catch((error: Error) => {
        console.error('Failed to approve token', error)
        throw error
      })
  }, [approvalState, tokenContract, syrupPoolContract, dispatch, syrupId, account, callback])

  return [approvalState, approve]
}

 
 
