import Web3 from 'web3'
import { HttpProviderOptions } from 'web3-core-helpers'
import { ChainId, ENVChainId } from 'sdk'
import getRpcUrl from 'utils/getRpcUrl'

const httpProviders = 
{
  [ChainId.BSCNET]: new Web3.providers.HttpProvider(getRpcUrl(ChainId.BSCNET), { timeout: 10000 } as HttpProviderOptions),

  [ChainId.HTNET]: new Web3.providers.HttpProvider(getRpcUrl(ChainId.HTNET), { timeout: 10000 } as HttpProviderOptions),
}

const web3NoAccount =
{
  [ChainId.BSCNET]: new Web3(httpProviders[ChainId.BSCNET]),

  [ChainId.HTNET]: new Web3(httpProviders[ChainId.HTNET]),
}

const getWeb3NoAccount = (chainId : ChainId) => 
{
  return web3NoAccount[chainId] ? web3NoAccount[chainId] : web3NoAccount[ENVChainId]
}

export default getWeb3NoAccount 
